<template>
  <div class="table2">
    <div class="header">
      <div>APP信息</div>
      <div class="header-right">
        <div class="search-wrap">
          <div class="search-left">
            <el-select size="mini" v-model="dataForm.aa" placeholder="请选择" class="select-inp">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
            <el-input placeholder="输入搜索内容" v-model="dataForm.bb" size="mini" class="search-inp" clearable></el-input>
          </div>
          <i class="el-icon-search icon-search"></i>
        </div>
        <button @click="createApp">+ 创建APP</button>
      </div>
    </div>
    <div>
      <el-table
      :data="tableData"
      style="width: 100%" :header-cell-style="{background:'#F1F0F5',color:'#1A1A1A'}">
      <el-table-column header-align="center" align="center" prop="name" label="名称" width="180"></el-table-column>
      <el-table-column header-align="center" align="center" prop="status" label="审核状态" width="180">
        <template slot-scope="scope">
          <span v-if="scope.row.status ==1" style="color: #58B00A">通过</span>
          <span v-else style="color: #B90909">失败</span>
        </template>
      </el-table-column>
      <el-table-column header-align="center" align="center" prop="ids" label="X-SPDB-Client-ID">
         <template slot-scope="scope">
           {{scope.row.ids}} <span class="copy" @click="copyContent(scope.row.ids)">复制</span>
         </template>
      </el-table-column>
       <el-table-column header-align="center" align="center" label="操作" width="200">
         <template slot-scope="scope" v-if="scope">
          <div class="handle-wrap">
            <div><img src="../../images/my/icon1.png"/>查看API</div>
            <div><img src="../../images/my/icon2.png"/>修改名称</div>
            <div><img src="../../images/my/icon3.png"/>开发者公钥</div>
            <div><img src="../../images/my/icon4.png"/>查看Secret</div>
          </div>
         </template>
        
       </el-table-column>
    </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dataForm:{
        aa: ''
      },
      options: [
        {
          value: "选项1",
          label: "api",
        },
        {
          value: "选项2",
          label: "场景",
        },
      ],
      tableData:[
        {
          name:'康师傅饮品快贷',
          status: 1,
          ids:'f4950395-f387-8599-d9385fhd984'
        },
        {
          name:'康师傅饮品快贷',
          status: 2,
          ids:'f4950395-3333-3333-d9385fhd984'
        }
      ]
    };
  },
  methods:{
    copyContent(ids){
      let oInput = document.createElement('input');
      oInput.value = ids;
      document.body.appendChild(oInput);
      oInput.select(); // 选择对象;
      console.log(oInput.value)
      document.execCommand("Copy"); // 执行浏览器复制命令
      this.$message({
        message: '复制成功',
        type: 'success'
      });
      oInput.remove()
    },
    createApp() {
      this.$router.push({name: 'createApp'})
    }
  }
};
</script>

<style lang="less" scoped>
.table2 {
  .header {
    display: flex;
    justify-content: space-between;
    height: 63px;
    align-items: center;
    padding:0 25px 0 34px;
    box-sizing: border-box;
    >div:first-child {
      font-size: 16px;
      font-weight: 500;
      color: #1A1A1A;
    }
    .header-right {
      display: flex;
      button {
        width: 107px;
        height: 32px;
        background: #FFFFFF;
        border: 1px solid #B90909;
        border-radius: 4px;
        font-size: 14px;
        font-family: PingFang SC;
        font-weight: 500;
        color: #B90909;
        margin-left: 22px;
      }
    }
  }
  .search-wrap {
    height: 30px;
    border-radius: 30px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #EEEEEE;
  }
  .search-wrap /deep/ .select-inp .el-input input {
    background: #e0e0e0;
    border: 0;
    color: #333;
    width: 140px;
    height: 30px;
    border-radius: 0;
  }
  .search-wrap  /deep/ .search-inp  input{
       background: #fff!important;
       font-size: 12px;
       outline: none;
       border: 0;
       &:focus{
         border: 0;
       }
  }
  .search-left {
    display: flex;
  }
  .search-wrap /deep/ input::-webkit-input-placeholder {
    color: #333;
  }
  .search-inp {
    width: 120px;
    font-size: 12px;
    background: none;
    border: 0;
  }
  .icon-search {
    cursor: pointer;
    color: #999;
    margin-right: 10px;
  }
  .copy {
    height: 20px;
    padding: 0 3px;
    line-height: 20px;
    background: #FF9C00;
    border-radius: 2px;
    font-size: 12px;
    font-family: PingFang SC;
    color: #FFFFFF;
    cursor: pointer;
  }
  .handle-wrap {
    div {
      display: flex;
      align-items: center;
      img {
        display: block;
        margin-right: 8px;
      }
      &:nth-of-type(1) > img{
        width: 18px;
        height: 18px;
        margin-left: 1px;
      }
      &:nth-of-type(2) > img{
        width: 18px;
        height: 18px;
        margin-left: 1px;
      }
      &:nth-of-type(3) > img{
        width: 16px;
        height: 18px;
        margin-left: 2px;
      }
      &:nth-of-type(4) > img{
        width: 19px;
        height: 14px;
      }
      color: #3B9BFF;
      font-size: 14px;
      line-height: 26px;
      text-align: center;
      cursor: pointer;
    }
  }
}
</style>


