<template>
  <div class="container my-wrap">
    <div class="my-wrap-left">
      <div v-for="(item,index) in menuList"  :key="index" class="menu-item" :class="{'active': active == index}" @click="selcetMenu(index)">
        <img :src="index == active ? item.icon1 : item.icon" alt="">
        {{item.title}}
      </div>
    </div>
    <div class="my-wrap-right">
      <table1 v-if="active == 0"></table1>
      <table2 v-if="active == 1"></table2>
      <table3 v-if="active == 2"></table3>
    </div>
  </div>
</template>

<script>
import table1 from './table1'
import table2 from './table2'
import table3 from './table3'
export default {
  components:{
    table1,
    table2,
    table3
  },
  data() {
    return {
      active: 0,
      menuList: [
        {
          title:'我的信息',
          icon: require('../../images/my/my-off.png'),
          icon1: require('../../images/my/my-on.png')
        },
        {
          title:'我的APP',
          icon: require('../../images/my/app-off.png'),
          icon1: require('../../images/my/app-on.png')
        },
         {
          title:'我的API',
          icon: require('../../images/my/api-off.png'),
          icon1: require('../../images/my/api-on.png')
        }
      ]
    }
  },
  methods:{
    selcetMenu(index) {
      this.active  = index
    }
  }
}
</script>
<style lang="less" scoped>
.my-wrap {
  display: flex;
  justify-content: space-between;
  margin-top: 44px;
  .my-wrap-left {
    width: 243px;
    min-height:calc(100vh - 66px - 100px - 44px);
    background: #fff;
    padding-top: 22px;
    box-sizing: border-box;
    box-shadow: 1px 1px 7px 0px rgba(0, 0, 0, 0.15);
    .menu-item {
      display: flex;
      align-items: center; 
      height: 54px;
      font-size: 14px;
      color: #717171;
      justify-content: center;
      cursor: pointer;
      img{ 
        width: 21px;
        display: block;
        margin-right: 18px;
      }
    }
    .active {
      background: #B90909;
      color: #fff;
    }
  }
  .my-wrap-right {
    width: 940px;
    min-height:calc(100vh - 66px - 100px - 44px);
    background: #fff;
    box-shadow: 1px 1px 7px 0px rgba(0, 0, 0, 0.15);
  }
}
</style>


