<template>
  <div class="table2">
    <div class="header">
      <div>
        <div @click="active =0" :class="{'active': active == 0}">正式环境API</div> | 
        <div  @click="active =1" :class="{'active': active == 1}">沙盒环境API</div>
      </div>
      <div class="header-right">
        <div class="search-wrap">
          <div class="search-left">
            <el-select size="mini" v-model="dataForm.aa" placeholder="请选择" class="select-inp">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
            <el-input placeholder="输入搜索内容" v-model="dataForm.bb" size="mini" class="search-inp" clearable></el-input>
          </div>
          <i class="el-icon-search icon-search"></i>
        </div>
        <button @click="createApp">+ 创建APP</button>
      </div>
    </div>
    <div>
      <el-table
      :data="tableData"
      style="width: 100%" :header-cell-style="{background:'#F1F0F5',color:'#1A1A1A'}">
      <el-table-column header-align="center" align="center" prop="name" label="API名称" width="280"></el-table-column>
      <el-table-column header-align="center" align="center" prop="byapp" label="所属APP"></el-table-column>
      <el-table-column header-align="center" align="center" prop="status" label="审核状态">
        <template slot-scope="scope">
          <span v-if="scope.row.status ==1" style="color: #58B00A">通过</span>
          <span v-else style="color: #B90909">失败</span>
        </template>
      </el-table-column>
      <el-table-column header-align="center" align="center" prop="time" label="申请时间" width="200">
         <template slot-scope="scope">
           {{scope.row.time}}
         </template>
      </el-table-column>
       <el-table-column header-align="center" align="center" label="操作" width="200">
         <template slot-scope="scope" v-if="scope">
          <div class="handle-wrap">
            <div @click="$router.push({name: 'apiDetail'})"><img src="../../images/my/icon1.png"/>查看API详情</div>
          </div>
         </template>
        
       </el-table-column>
    </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: 0,
      dataForm:{
        aa: ''
      },
      options: [
        {
          value: "选项1",
          label: "api",
        },
        {
          value: "选项2",
          label: "场景",
        },
      ],
      tableData:[
        {
          name:'网贷待签名电子合同查询交易',
          byapp: '弘昊罗莱快贷',
          status: 1,
          time:'2021-03-30 14:07:21'
        },
      ]
    };
  },
  methods:{
    createApp() {
      this.$router.push({name: 'createApp'})
    }
  }
};
</script>

<style lang="less" scoped>
.table2 {
  .header {
    display: flex;
    justify-content: space-between;
    height: 63px;
    align-items: center;
    padding:0 25px 0 34px;
    box-sizing: border-box;
    >div:first-child {
      font-size: 16px;
      // font-weight: 500;
      color: #999999;
      display: flex;
      align-items: center;
      cursor: pointer;
      div {
        padding: 0 20px;
        &:first-child {
          padding-left: 0;
        }
      }
    }
    .header-right {
      display: flex;
      button {
        width: 107px;
        height: 32px;
        background: #FFFFFF;
        border: 1px solid #B90909;
        border-radius: 4px;
        font-size: 14px;
        font-family: PingFang SC;
        font-weight: 500;
        color: #B90909;
        margin-left: 22px;
      }
    }
  }
  .search-wrap {
    height: 30px;
    border-radius: 30px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #EEEEEE;
  }
  .search-wrap /deep/ .select-inp .el-input input {
    background: #e0e0e0;
    border: 0;
    color: #333;
    width: 140px;
    height: 30px;
    border-radius: 0;
  }
  .search-wrap  /deep/ .search-inp  input{
       background: #fff!important;
       font-size: 12px;
       outline: none;
       border: 0;
       &:focus{
         border: 0;
       }
  }
  .search-left {
    display: flex;
  }
  .search-wrap /deep/ input::-webkit-input-placeholder {
    color: #333;
  }
  .search-inp {
    width: 120px;
    font-size: 12px;
    background: none;
    border: 0;
  }
  .icon-search {
    cursor: pointer;
    color: #999;
    margin-right: 10px;
  }
  .handle-wrap {
    min-height: 97px;
    display: flex;
    align-items: center;
    justify-content: center;
    div {
      color: #3B9BFF;
      font-size: 14px;
      text-align: center;
      cursor: pointer;
      display: flex;
      align-items: center;
      img {
        width: 18px;
        height: 18px;
        display: block;
        margin-right: 8px;
      }
    }
  }
  .active {
    color: #1A1A1A;
    font-weight: 500;
  }
}
</style>


